import styled from 'styled-components'

export const Authform = styled.div`
	.container {
		position: relative;
		min-width: 100vw;
		min-height: 100vh;
		background: #fff;
		overflow: hidden;
		margin-top: 4rem;
		&:before {
			content: '';
			position: absolute;
			width: 2000px;
			height: 2000px;
			border-radius: 50%;
			background: linear-gradient(-45deg, #3f51b5, #4481eb);
			top: -10%;
			right: 48%;
			transform: translateY(-50%);
			z-index: 6;
			transition: 1.8s ease-in-out;
		}
	}
	.forms-container {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
	}
	.signin-signup {
		position: absolute;
		top: 50%;
		left: 75%;
		width: 50%;
		transform: translate(-50%, -50%);
		display: grid;
		grid-template-columns: 1fr;
		z-index: 5;
		transition: 1s .7s ease-in-out;
	}
	form {
		padding: .5rem;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		grid-column: 1 / 2;
		grid-row: 1 / 2;
		transition: .2s .7s ease-in-out;
		&.sign-in-form {
			z-index: 2;
		}
		&.sign-up-form {
			z-index: 1;
			opacity: 0;
		}
	}
	.title {
		font-size: 2.2rem;
		color: #444;
		margin-bottom: 10px;
	}
	.input-field {
		max-width: 380px;
		width: 100%;
		height: 55px;
		background-color: #f0f0f0;
		margin: 10px 0;
		border-radius: 55px;
		display: grid;
		grid-template-columns: 15% 85%;
		padding: 0 .4rem;
		border: 1px solid ${props => props.inputColor};
		 i {
			text-align: center;
			line-height: 55px;
			color: #acacac;
			font-size: 1.1rem;
		}
		 input {
			background: none;
			outline: none;
			border: none;
			line-height: 1;
			font-weight: 600;
			font-size: 1.1rem;
			color: #333;
		}
			&input::placeholder {
			color: #aaa;
			font-weight: 500;
		}
	}
	.btn {
		width: 150px;
		height: 49px;
		border: none;
		outline: none;
		border-radius: 49px;
		cursor: pointer;
		background-color: #5995fd;
		color: #fff;
		text-transform: uppercase;
		font-weight: 600;
		margin: 10px 0;
		transition: .5s;
		&:hover {
			background-color: #4d84e2;
		}
	}
	.social-text {
		padding: .7rem 0;
		font-size: 1rem;
	}
	.social-media {
		display: flex;
		justify-content: center;
	}
	.social-icon {
		height: 46px;
		width: 46px;
		border: 1px solid #333;
		margin: 0 0.45rem;
		display: flex;
		align-items: center;
    justify-content: center;
		text-decoration: none;
		color: #333;
		font-size: 1.1rem;
		border-radius: 50%;
		transition: .3s;
		&:hover {
			color: #4481eb;
			border-color: #4481eb;
		}
	}
	.panels-container {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		display: grid;
		grid-template-columns: repeat(2, 1fr);
	}
	.panel {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		justify-content: space-around;
		text-align: center;
		z-index: 7;
		.content {
			color: #fff;
			transition: transform 0.9s ease-in-out;
			transition-delay: 0.6s;
		}
		p {
			font-size: 0.95rem;
			padding: 0.7rem 0;
		}
		h3 {
			font-weight: 600;
			line-height: 1;
			font-size: 1.5rem;
		}
	}
	.left-panel {
		pointer-events: all;
		padding: 3rem 17% 2rem 12%;
	}
	.right-panel {
		pointer-events: none;
		padding: 3rem 17% 2rem 12%;
		&.content,.image {
			transform: translateX(800px);
		}
	}
	.image {
		width: 100%;
		transition: 1.1s .4s ease-in-out;
	}
	.btn.transparent {
		margin: 0;
		background: none;
		border: 2px solid #fff;
		width: 130px;
		height: 41px;
		font-weight: 600;
		font-size: .8rem;
	}
	/* Animation */
	.container.sign-up-mode:before {
  transform: translate(100%, -50%);
  right: 52%;
	}

	.container.sign-up-mode .left-panel .image,
	.container.sign-up-mode .left-panel .content {
		transform: translateX(-800px);
	}

	.container.sign-up-mode .signin-signup {
		left: 25%;
	}

	.container.sign-up-mode form.sign-up-form {
		opacity: 1;
		z-index: 2;
	}

	.container.sign-up-mode form.sign-in-form {
		opacity: 0;
		z-index: 1;
	}

	.container.sign-up-mode .right-panel .image,
	.container.sign-up-mode .right-panel .content {
		transform: translateX(0%);
	}

	.container.sign-up-mode .left-panel {
		pointer-events: none;
	}

	.container.sign-up-mode .right-panel {
		pointer-events: all;
	}
	/* @media */
	@media (max-width: 870px) {
  .container {
    min-height: 800px;
    height: 100vh;
  }
  .signin-signup {
    width: 100%;
    top: 95%;
    transform: translate(-50%, -100%);
    transition: 1s 0.8s ease-in-out;
  }

  .panels-container {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 2fr 1fr;
  }

  .panel {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 2.5rem 8%;
    grid-column: 1 / 2;
  }

  .right-panel {
    grid-row: 3 / 4;
  }

  .left-panel {
    grid-row: 1 / 2;
  }

  .image {
    width: 200px;
    transition: transform 0.9s ease-in-out;
    transition-delay: 0.6s;
  }

  .btn.transparent {
    width: 110px;
    height: 35px;
    font-size: 0.7rem;
  }

  .container:before {
    width: 1500px;
    height: 1500px;
    transform: translateX(-50%);
    left: 30%;
    bottom: 68%;
    right: initial;
    top: initial;
    transition: 2s ease-in-out;
  }

  .container.sign-up-mode:before {
    transform: translate(-50%, 100%);
    bottom: 32%;
    right: initial;
  }

  .container.sign-up-mode .left-panel .image,
  .container.sign-up-mode .left-panel .content {
    transform: translateY(-300px);
  }

  .container.sign-up-mode .right-panel .image,
  .container.sign-up-mode .right-panel .content {
    transform: translateY(0px);
  }

  .right-panel .image,
  .right-panel .content {
    transform: translateY(300px);
  }

  .container.sign-up-mode .signin-signup {
    top: 5%;
    transform: translate(-50%, 0);
  }
}

@media (max-width: 570px) {
  form {
    padding: 0 1.5rem;
  }

  .image {
    display: none;
  }
  .panel .content {
    padding: 0.5rem 1rem;
  }
  .container {
    padding: 1.5rem;
  }

  .container:before {
    bottom: 72%;
    left: 50%;
  }

  .container.sign-up-mode:before {
    bottom: 28%;
    left: 50%;
  }
}
`